<template>
    <div class="container-fluid">
        <br>
        <div class="container">
            <form class="form">
                <div class="form-group">
                    <select class="form-control" @change="switchClass" v-model="currentSelectedClass"
                            name="currentClass" id="current_class_selector">
                        <option value="0000">点击选择你要编辑的课程</option>
                        <option v-for="className in classList" :value="className">{{className}}</option>
                    </select>
                </div>
            </form>
        </div>
        <hr>
        <div class="ppt-preview" v-if="pages !== null" style="display: flex;justify-content: flex-start;overflow-x: scroll;">
            <div class="ppt-preview-item" v-for="(page,index) in pages"
                 :key="index">
                <h6 :class="{'text-danger': index === currentPageIndex}" style="text-align: center">{{index + 1}}</h6>
                <img :src="'http://localhost:5500/' + currentSelectedClass + '/' + index + '.jpg'"
                     :class="{'border-red-3px': index === currentPageIndex}"
                     width="160" height="90" @click="switchPage(index)" style="margin: 5px;border: 1px solid gainsboro;">
            </div>
        </div>
        <hr v-if="pages !== null">
        <div class="tools container" v-if="pages !== null" style="text-align: right;">
            <button class="btn btn-danger" style="margin: 20px;"
                    @click="deleteNote(lastActiveNote.dataset.index)"
                    :disabled="currentActiveNote == null && lastActiveNote == null">
                删除该条笔记
            </button>
        </div>
        <div class="ppt-edit"
             style="display: flex;
                    position: relative;
                    justify-content: center"
             v-if="pages !== null">
            <div class="ppt-page" style="position: relative;
                                         height: 540px;
                                         width:1400px;">

                <img id="ppt_edit_background" width="960" height="540" alt="PPT edit backgournd"
                     style="position: absolute;
                            top:0;"
                     :src="'http://localhost:5500/' + currentSelectedClass + '/' + currentPageIndex + '.jpg'">

                <canvas id="canvas_note_indicator" style="position: absolute;left: 0;top: 0;border: 1px solid gainsboro;"
                        class="ppt-page-canvas" width="1400" height="540">
                </canvas>

                <div class="ppt-notes" style="padding: 0;
                                              margin: 0;
                                              position: absolute;
                                              top: 0;
                                              right: 0;
                                              width: 300px;
                                              height: 540px;
                                              overflow-y: scroll;"
                     @scroll="notesScroll">
                    <div v-for="(note, index) in pages[currentPageIndex].pageNotes"
                         :id="'ppt_note_item_' + index"
                         :key="currentSelectedClass + '-' + currentPageIndex + '-' + index"
                         class="note-content hide-scroll-bar"
                         style="height: 80px;
                                border: 1px solid gray;
                                padding: 5px;
                                margin: 10px 0;
                                overflow-y: scroll;
                                outline: none"
                         contenteditable="true"
                         :data-index="index"
                         :data-x="note.x"
                         :data-y="note.y"
                         :data-width="note.width"
                         :data-height="note.height"
                         @focus="noteFocus($event)"
                         @blur="noteBlur($event)">
                        {{note.noteContent}}
                    </div>
                </div>

                <my-canvas-rect width="960" height="540" @rect="addNewNote($event)"></my-canvas-rect>
            </div>
        </div>
    </div>
</template>

<script>
    import {
        convertCoordinate,
        post,
        get,
        showLoading,
        hideCover,
        showCover,
        hideLoading
    } from '../../../../common/src/utils'
    import MyCanvasRect from '../../../../common/src/components/MyCanvasRect'

    export default {
        name: 'EditFile',
        components: {
            MyCanvasRect
        },
        data() {
            return {
                classList: [],
                pages: null,
                currentSelectedClass: '0000',
                currentPageIndex: 0,
                currentActiveNote: null,
                lastActiveNote: null,
            }
        },
        methods: {
            async switchClass() {
                if (this.currentSelectedClass === '0000') {
                    this.pages = null
                    this.currentPageIndex = 0
                    return
                }
                this.pages = null
                this.currentPageIndex = 0
                showLoading()
                try {
                    const data = JSON.parse(await get('http://localhost:5000/get_class_data?className=' + this.currentSelectedClass))
                    if (data.ok) {
                        this.pages = data.data.pages
                    } else {
                        alert(data.message)
                    }
                } catch (e) {
                    console.log(e)
                    alert('课程数据加载失败, 请检查网络连接')
                }
                hideLoading()
            },
            switchPage(pageIndex) {
                this.currentPageIndex = pageIndex
            },
            savePageNotes(pageIndex) {
                post('http://localhost:5000/update_class_data', JSON.stringify({
                    className: this.currentSelectedClass,
                    pageIndex: this.currentPageIndex,
                    pageNotes: this.pages[this.currentPageIndex].pageNotes
                }))
            },
            clearNoteIndicator() {
                const canvas = document.getElementById('canvas_note_indicator')
                const ctx = canvas.getContext('2d')
                ctx.clearRect(0, 0, 1400, 700)
            },
            drawNoteIndicator(note) {
                const top = note.offsetTop - note.offsetParent.scrollTop
                const left = 1100
                const canvas = document.getElementById('canvas_note_indicator')
                const ctx = canvas.getContext('2d')
                ctx.clearRect(0, 0, 1400, 700)
                ctx.lineWidth = 3
                ctx.strokeStyle = 'red'
                ctx.strokeRect(note.dataset.x, note.dataset.y, note.dataset.width, note.dataset.height)

                ctx.beginPath()
                ctx.moveTo(note.dataset.x, note.dataset.y)
                ctx.lineTo(1050, note.dataset.y)
                ctx.lineTo(left + 1, top + 20)
                ctx.stroke()
            },
            noteFocus(event) {
                this.currentActiveNote = event.target
                this.drawNoteIndicator(event.target)
            },
            noteBlur(event) {
                const self = this
                this.pages[this.currentPageIndex].pageNotes[this.currentActiveNote.dataset.index].noteContent = this.currentActiveNote.innerText
                this.clearNoteIndicator()
                this.savePageNotes()
                this.lastActiveNote = self.currentActiveNote
                this.currentActiveNote = null
                setTimeout(function () {
                    self.lastActiveNote = null
                }, 300)
            },
            notesScroll() {
                if (this.currentActiveNote) {
                    this.currentActiveNote.blur()
                }
            },
            addNewNote(e) {
                const x1 = e.p1[0]
                const y1 = e.p1[1]
                const x2 = e.p2[0]
                const y2 = e.p2[1]
                const coordinate = convertCoordinate(x1, y1, x2, y2)
                coordinate.noteContent = '请输入笔记内容'
                const length = this.pages[this.currentPageIndex].pageNotes.length
                this.pages[this.currentPageIndex].pageNotes.splice(length, 0, coordinate)
                this.$set(this.pages[this.currentPageIndex], 'pageNotes', this.pages[this.currentPageIndex].pageNotes)
                setTimeout(() => {
                    const newNoteId = 'ppt_note_item_' + length
                    document.getElementById(newNoteId).scrollIntoView()
                    setTimeout(() => {
                        document.getElementById(newNoteId).focus()
                        this.addingNote = false
                    }, 300)
                }, 300)
            },
            deleteNote(index) {
                if (index) {
                    this.pages[this.currentPageIndex].pageNotes.splice(index, 1)
                    this.$set(this.pages[this.currentPageIndex], 'pageNotes', this.pages[this.currentPageIndex].pageNotes)
                }
                this.savePageNotes()
            }
        },
        async mounted() {
            showLoading()
            try {
                const data = JSON.parse(await get('http://localhost:5000/get_class_list'))
                if (data.ok) {
                    this.classList = data.data
                } else {
                    alert(data.message)
                }
            } catch (e) {
                console.log(e)
                alert('加载课程列表失败, 请检查网络连接状况')
            }
            hideLoading()
        }
    }
</script>

<style scoped>
    .border-red-3px {
        border: 3px solid red !important;
    }

    .note-content:focus {
        border: 3px solid red !important;
    }
</style>
