<template>
    <div style="position: relative;margin: 0;padding: 0;" :style="{width: width + 'px', height: height + 'px'}">
        <canvas style="position: relative;margin: 0;padding: 0;"
                :style="{width: width + 'px', height: height + 'px'}" :width="width" :height="height" ref="canvas"></canvas>
    </div>
</template>

<script>
    export default {
        name: 'MyCanvasRect',
        props: ['width', 'height'],
        data: function () {
            return {
                drawing: false,
                p1: null, // start point
                p2: null, // end point
                ctx: null,
                canvas: null,
            }
        },
        mounted() {
            this.canvas = this.$refs.canvas
            this.ctx = this.canvas.getContext('2d')
            this.setListeners()
        },
        methods: {
            clearCanvas() {
                this.ctx.clearRect(0, 0, this.width, this.height)
            },
            draw() {
                if (this.p1 === null || this.p2 === null || this.drawing === false) {
                    return
                }
                this.clearCanvas()
                this.ctx.strokeStyle = 'red'
                this.ctx.strokeWidth = 1
                this.ctx.lineWidth = 1
                const x1 = this.p1[0]
                const y1 = this.p1[1]
                const x2 = this.p2[0]
                const y2 = this.p2[1]
                this.ctx.beginPath()
                this.ctx.moveTo(x1, y1)
                this.ctx.lineTo(x1, y2)
                this.ctx.lineTo(x2, y2)
                this.ctx.lineTo(x2, y1)
                this.ctx.lineTo(x1, y1)
                this.ctx.stroke()
            },
            mouseDownListener(e) {
                this.canvas.setPointerCapture(e.pointerId)
                this.drawing = true
                this.p1 = [e.layerX, e.layerY]
            },
            mouseMoveListener(e) {
                if (this.drawing) {
                    this.p2 = [e.layerX, e.layerY]
                    if (this.p2[0] < 0) {
                        this.p2[0] = 0
                    }
                    if (this.p2[0] > this.width) {
                        this.p2[0] = this.width - 1
                    }
                    if (this.p2[1] < 0) {
                        this.p2[1] = 0
                    }
                    if (this.p2[1] > this.height) {
                        this.p2[1] = this.height - 1
                    }
                    this.draw()
                }
            },
            mouseUpListener(e) {
                this.canvas.releasePointerCapture(e.pointerId)
                this.$emit('rect', {
                    p1: this.p1,
                    p2: this.p2
                })
                this.reset()
            },
            reset() {
                this.p1 = null
                this.p2 = null
                this.drawing = false
                this.clearCanvas()
            },
            setListeners() {
                this.canvas.addEventListener('pointerdown', this.mouseDownListener)
                this.canvas.addEventListener('pointermove', this.mouseMoveListener)
                this.canvas.addEventListener('pointerup', this.mouseUpListener)
            }
        }
    }
</script>

<style scoped>
    canvas {
        border: 1px solid black;
        box-sizing: border-box;
    }
</style>
