<template>
    <div id="app">
        <nav class="navbar navbar-expand-sm navbar-dark bg-primary">

            <span class="navbar-brand">Human AI @ JLU</span>

            <span class="navbar-text pr-5">智慧课堂</span>

            <ul class="navbar-nav">

                <li class="nav-item px-2" v-if="login">
                    <a class="nav-link" href="#">你好,{{username}}</a>
                </li>

                <li class="nav-item px-2" :class="{active: nav === 1}"  v-if="!login"
                    id="nav_upload" @click="switchNav(1)">
                    <a class="nav-link" href="#">教师登录</a>
                </li>

                <li class="nav-item px-2" :class="{active: nav === 2}"
                    id="nav_edit" @click="switchNav(2)">
                    <a class="nav-link" href="#">上传课程</a>
                </li>

                <li class="nav-item px-2" :class="{active: nav === 3}"
                    id="nav_display" @click="switchNav(3)">
                    <a class="nav-link" href="#">编辑课程</a>
                </li>

            </ul>

        </nav>
        <div class="container-fluid">
            <teacher-login v-if="nav === 1" @login="loginSuccess($event)"></teacher-login>
            <upload-file v-if="nav === 2"></upload-file>
            <edit-file v-if="nav === 3"></edit-file>
        </div>
    </div>
</template>

<script>
    import 'bootstrap/dist/css/bootstrap.css'
    import '@fortawesome/fontawesome-free/css/all.css'
    import '../../../common/src/css/scroll-bar.css'
    import UploadFile from './components/UploadFile'
    import EditFile from "./components/EditFile";
    import TeacherLogin from './components/TeacherLogin.vue'

    export default {
        name: 'app',
        components: {
            UploadFile,
            EditFile,
            TeacherLogin,
        },
        data() {
            return {
                nav: 1,
                username: null,
                login: false
            }
        },
        methods: {
            switchNav(new_nav) {
                if (this.nav === 1) {
                    if (!this.login) {
                        return
                    }
                }
                this.nav = new_nav
            },
            loginSuccess(username) {
                this.login = true
                this.username = username
                this.switchNav(2)
            }
        },
        mounted() {
        }
    }
</script>

<style scoped>
    #app
    {
        position: relative;
        height: 100%;
        font-family: 'Avenir', Helvetica, Arial, sans-serif;
        -webkit-font-smoothing: antialiased;
        -moz-osx-font-smoothing: grayscale;
        color: #2c3e50;
        background: transparent;
        overflow-y: scroll !important;
    }
    #global_cover
    {
        position: absolute;
        left: 0;
        top:0;
        width: 100%;
        height: 100%;
        z-index: 997;
        background-color: gray;
        opacity: 0.5;
        display: none;
    }
    nav
    {
        justify-content: space-between !important;
        background: transparent !important;
        color: black !important;
        border-bottom: 1px solid black;
    }
    span,
    li a {
        color: black !important;
    }
    .active a {
        font-weight: bolder !important;
    }
</style>
