<template>
    <div class="container">
        <div class="title">
            <h3>上传PPT文件</h3>
        </div>
        <hr>
        <br><br>
        <div class="form-wrapper">
            <form id="upload_file_form" class="form"  @submit.prevent="upload">
                <div class="form-group">
                    <label for="upload_class_name">课程名称:</label>
                    <input id="upload_class_name" v-model="className" required>
                </div>
                <br><br>
                <div class="label">选择要上传的pdf文件</div>
                <div class="input-group">
                    <div class="custom-file">
                        <input id="upload_file_input" type="file" class="custom-file-input"
                               accept="application/pdf" required>
                        <label class="custom-file-label" for="upload_file_input" data-browse="选择文件">点击选择要上传的pdf文件</label>
                    </div>
                </div>
                <br><br>
                <button type="submit">上传</button>
            </form>
        </div>
    </div>
</template>

<script>
    import {post, file2dataURL, showLoading, hideLoading} from '../../../../common/src/utils'
    import $ from 'jquery'
    import bsCustomFileInput from 'bs-custom-file-input'
    export default {
        name: 'UploadFile',
        data: function() {
            return {
                className: 'MyClass'
            }
        },
        methods: {
            async upload() {
                const file = document.getElementById('upload_file_input')
                const fileArray = file.files
                if (fileArray.length >1) {
                    alert('一次最多选择一个文件')
                }
                if (fileArray.length === 0) {
                    alert('请选择文件')
                }
                const dataURL = await file2dataURL(fileArray[0])
                try {
                    showLoading()
                    await post('http://localhost:5000/upload_class', JSON.stringify({
                        className: this.className,
                        base64Data: dataURL
                    }))
                    alert('上传成功')
                    hideLoading()
                }
                catch (e) {
                    console.log(e)
                    alert('上传失败')
                }
                // document.getElementById('upload_file_form').reset()
            }
        },
        mounted() {
            $(document).ready(function () {
                bsCustomFileInput.init()
            })
        }
    }
</script>

<style scoped>
    .title {
        padding-top: 40px;
    }
    input {
        width: 100%;
        color: black;
        background-color: transparent;
        border-color: transparent;
        border-bottom: 1px solid black;
        outline: none;
        font-size: 1.5em;
        padding: .5em 0;
    }
    input:focus {
        outline: none !important;
    }
    button {
        border: 1px solid black;
        background-color: transparent;
        padding: 5px 20px;
        color: black;
        border-radius: 5px;
        font-size: 1.5em;
    }
    .label {
        font-size: 16px;
        color: black;
        padding-bottom: 22px;
    }
    .custom-file-input {
        background-color: transparent;
        border-color: transparent;
        border-bottom: 1px solid black;
        visibility: hidden;
    }
    .custom-file-label {
        font-size: 1.5em;
        border-color: transparent;
        border-bottom: 1px solid black;
        background-color: transparent;
        padding-left: 0;
    }
    .custom-file-label::after {
        background-color: transparent;
        color: transparent;
    }
</style>
